.switch-theme {
  cursor: pointer;
  background-color: var(--accent-color);
  border-style: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 10px;
  display: flex;
}

.switch-icon {
  width: inherit;
  height: inherit;
  fill: #fff;
  transition: transform .5s cubic-bezier(.39, .575, .565, 1);
}

.switch-icon .hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.switch-icon:hover, .switch-icon:focus {
  transform: rotate(360deg);
}

