.backdrop {
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background-color: rgba(var(--accent-color), .5);
  position: fixed;
  inset: 0;
}

.backdrop.show {
  opacity: 1;
  visibility: visible;
  pointer-events: visible;
}

.backdrop.show .modal__wrapper {
  animation: 1s cubic-bezier(.39, .575, .565, 1) both animModal;
}

.modal__wrapper {
  background-color: var(--primary-background-color);
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
  transform: translate(0, -100%);
}

@media screen and (max-width: 1279px) {
  .modal__wrapper {
    height: 100vh;
    max-height: 100vh;
    bottom: 0;
  }
}

