  .modal__wrapper {
    height: auto;
    max-height: 80%;
  }
}

.backdrop__close {
  cursor: pointer;
  background-color: var(--accent-color);
  fill: var(--primary-background-color);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  padding: 5px;
  display: inline-flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.backdrop__close:hover, .backdrop__close:focus {
  fill: var(--secondary-text-color);
}

.backdrop__close svg {
  width: inherit;
  height: inherit;
}

@keyframes animModal {
  0% {
    opacity: 0;
    transform: translate(0, -100%);
  }

  50% {
    opacity: 0;
    transform: translate(0, -100%);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

