}

.genre__container {
  gap: 50px;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

.genre:not(:last-child) .genre__container:after {
  content: "->";
  color: var(--accent-color);
  width: 30px;
  height: 30px;
  font-family: sans-serif;
  font-weight: bold;
  animation: 2s cubic-bezier(.215, .61, .355, 1) infinite pointerSliderRight;
  display: flex;
  position: absolute;
  bottom: -28px;
  right: 10px;
  transform: translateX(0);
}

.genre:not(:first-child) .genre__container:before {
  content: "<-";
  color: var(--accent-color);
  width: 30px;
  height: 30px;
  font-family: sans-serif;
  font-weight: bold;
  animation: 2s cubic-bezier(.215, .61, .355, 1) infinite pointerSliderLeft;
  display: flex;
  position: absolute;
  bottom: -28px;
  left: 10px;
  transform: translateX(0);
}

