@media screen and (min-width: 600px) {
  .book__wrapper {
    width: 70%;
  }
}

.book__name {
  color: var(--secondary-text-color);
  margin-bottom: 5px;
}

.book__name:first-letter {
  color: var(--accent-color);
  font-family: Righteous, sans-serif;
  font-size: 40px;
}

@media screen and (max-width: 599px) {
  .book__name {
    font-size: 18px;
  }
}

.book__author {
  color: var(--accent-color);
  margin-bottom: 5px;
  font-family: Righteous, sans-serif;
  font-size: 15px;
}

.book__date {
  margin-bottom: 15px;
  font-size: 15px;
}

.book__description {
  margin-bottom: 20px;
  font-size: 15px;
}

.book__btn {
  cursor: pointer;
  border: 2px solid var(--accent-color);
  color: var(--secondary-text-color);
  background-color: var(--accent-color);
  padding: 10px 15px;
  transition: background-color .25s linear;
}

.book__btn:hover, .book__btn:focus {
  background-color: #0000;
}

.book__btn.hidden {
  display: none;
}

.book__span {
  font-size: 15px;
}

.series {
  transition: box-shadow 1s cubic-bezier(.215, .61, .355, 1);
}

.series:hover, .series:focus {
  box-shadow: 1px 1px 1px 1px var(--accent-color);
}

.btnUp {
  z-index: 500;
  fill: var(--primary-text-color);
  background-color: var(--accent-color);
  cursor: pointer;
  border-style: none;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 5px;
  transition: color .25s linear;
  display: inline-flex;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.btnUp.hide {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.btnUp:hover, .btnUp:focus {
  fill: var(--primary-background-color);
}

